.app {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #007BFF; /* Dalianの青色 */
  color: white;
}

.container {
  width: 100%;
  max-width: 600px;
  padding: 20px;
  box-sizing: border-box;
  background-color: rgba(255, 255, 255, 0.8);
  border-radius: 10px;
  color: black;
}

.question {
  font-size: 1.8rem;
  margin-bottom: 20px;
}

.choice {
  margin: 15px 0;
}
.button {
  font-size: 1.5rem; /* フォントサイズを大きくする */
  padding: 15px 30px; /* パディングを増やす */
  margin: 5px;
  width: 100%; /* ボタンの幅を100%にする */
  background-color: #007BFF;
  color: white;
  border: none;
  border-radius: 5px;
}

.button:disabled {
  background-color: #ccc;
}

.next-button {
  margin-top: 20px;
  background-color: #28a745;
}

.image {
  margin-top: 20px;
  max-width: 100%;
}


.result {
  display: block;
  margin-top: 10px;
  font-size: 1.2rem;
  color: #dc3545; /* 赤色 */
}



/* 他のスタイルはそのまま */
.score {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 1.5rem;
  color: white;
}

